@import '_styles/variables';
@import '_styles/mixins';

.SiteHeader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 62px;
  background: rgba(#ffffff, 0.95);
  text-align: center;;

  &__logo {
    position: absolute;
    left: 8px;
    top: 0;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 1;
    line-height: 0;
  }

  &__logoIcon {
    width: 90px;
    fill: currentColor;
  }

  &__nav {
    display: inline-flex;
    height: 100%;
    align-items: center;
    justify-content: center;
  }

  &__social {
    position: absolute;
    right: 8px;
    top: 0;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__link {
    display: inline-block;
    @include fontCaps;
    padding: 0.75em;
    text-decoration: none;
    opacity: 0.4;
    vertical-align: middle;
    margin: 0 4px;

    &:hover,
    &:active {
      opacity: 0.75;
    }

    &--active {
      opacity: 1;

      &:hover,
      &:active {
        opacity: 1;
      }
    }

    &--icon {
      padding: 0.5em;
      margin: 0 0.5em;
      line-height: 0;
      transform: scale(1); // Need this to force GPU acceleration on the link (prevent wobbling effect when hovering over SVG link)
    }
  }

  &__icon {
    width: 1.5em;
    fill: currentColor;
  }
}
