@import '_styles/variables';
@import '_styles/mixins';

.MoreReading {
  background: transparent url('../_images/misc/bannerPattern.png') repeat;
  padding: calc(12px + 2vw) 0;

  &__title {
    margin-top: 0;
    text-align: center;;
    font-size: 2rem;
  }

  &__grid {
    grid-auto-columns: unset;
    grid-auto-flow: unset;
    grid-template-columns: 1fr;

    @include respondAbove (s) {
      grid-template-columns: 1fr 1fr;
    }

    @include respondAbove (l) {
      grid-template-columns: unset;
      grid-auto-columns: 1fr;
      grid-auto-flow: column;
      }
  }

  &__card {
    position: relative;
    background-color: #FFF;
    padding-bottom: 4rem;
    text-align: center;
  }

  &__cardTitle {
    padding: 1.5rem 1.5rem 0;
    margin: 0;
    line-height: 1.5;
  }

  &__cardTimeAgo {
    position: absolute;
    left: 0;
    bottom: 0;
    display: grid;
    align-items: center;
    width: 100%;
    height: 4rem;
    @include fontCaps;
    opacity: 0.3;
    margin: 0;
    font-size: 0.625em;
  }
}
